.translator2 {
    width: 100%;
    // margin-left: 10%;
    margin: 30px auto;
  }
  
  .rowWrapper2 {
    display: flex;
    flex-direction: row;
  }
  
  .translatorContainer2 {
    width: 100%;
  }
  
  .textBox2 {
    min-height: 200px;
    width: 100%;
    padding: 10px 15px;
    padding-right: 80px;
    font-size: 18px;
    border-radius: 7px;
  }
  
  .inputForm textarea {
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    box-shadow: -2px 5px 10px -5px #000000bf;
    outline: none;
    border: none;
    resize: none;
  }
  
  .inputForm textarea::placeholder,
  .outputPlaceholder {
    color: dimgray;
    user-select: none;
    cursor: text;
  }
  
  .inputForm {
    position: relative;
  }
  
  .iconBtn {
    position: absolute;
    font-size: 20px;
    cursor: pointer;
    margin: 13.5px 20px;
    transition: 0.2s ease-in-out;
    right: 0;
  }
  
  .iconBtn:hover {
    color: #696969bf;
  }
  
  .speaker {
    font-size: 27px;
    bottom: 2px;
    right: 30px;
  }
  
  .copyBtn {
    bottom: 5px;
  }
  
  .copyOutputBtn {
    bottom: -2px;
  }
  
  .outputBox {
    position: relative;
    box-shadow: 2px 5px 10px -5px rgba(0, 0, 0, 0.75);
    margin-top: 30px;
  }
  
  .loadingOutput {
    font-size: 20px;
    font-weight: bold;
    color: #6c757d;
    letter-spacing: 2px;
  }
  
  
  .topRow {
    display: flex;
    align-items: center;
    height: 30px;
    margin: 7px 0px;
  }
  
  .btnTranslate,
  .topRow select {
    margin-left: auto;
    width: 7em;
  }
  
  .translation-error {
    color: #ff1234;
  }
  
  @media (max-width: 1279px) {
    .translator2 {
      width: 100%;
      margin-left: 0%;
    }
  }
  
  @media (max-width: 768px) {
    .translatorContainer2 {
      width: 100%;
    }
  
    .rowWrapper2 {
      flex-direction: column;
    }
  }