@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');
$backgroundColor: white;
$textcolor:#000000;
$borderLeftColor:#57330c;

.navbarMenu {
  height:100vh;
  background-color:$backgroundColor;
  box-shadow: 1px 1px 7px rgba(0,0,0,0.15);
  transition: all 0.5s ease;
  // margin-left: -120px;
  position: relative;
  display: none;
}
.burger{
  display:flex;
  justify-content: flex-end;
}
.burger {
  padding: 25px 25px 0px 10px;
  cursor: pointer;
}
.navbar__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
}
.navbar__li {
  display: inline-block;
  padding:0px 0px 0px 12px;
  position: relative;
  top:2.5px;
  font-size: 16px;
  letter-spacing: 0px;
  // line-height: 38px;
  color: $textcolor;
  font-family: "Lato";
  font-weight: 400;
  // margin-left: -100px;
}
.navbar__liBox:first-child {
  margin-top:53px;
  
}
.navbar__liBox {
  height: 43px;
  margin-top:15px;
  margin-left: -35px;

}
.navbar__liBox:hover {
  border-left: 7px solid $borderLeftColor;
  cursor: pointer;
}

@media screen and (min-width: 768px){
  .navbarMenu{
    display: block;
  }
}