

.navbar {
    display: none;
    width: 90vw;
    margin-left: 0;
  }
  
 .navbar2 {
    width: 90vw;
    margin-left: 0;
  }

  .navbar .nav-container li {
    list-style: none;
  }
  
  .navbar .nav-container a {
    text-decoration: none;
    color: #0e2431;
    font-weight: 500;
    font-size: 1.3rem;
    padding: 0.7rem;
  }
  
  .navbar .nav-container a:hover{
      font-weight: bolder;
  }
  
  .nav-container {
    display: block;
    position: relative;
    height: 60px;
  }
  
  .logo {
    position: absolute;
    top: -23px;
    left: 15px;
    font-size: 1.2rem;
    color: #0e2431;

    & .Logo{
      display: block;
      width: 160px;
      height: 80px; 
    }
  }
.mobile_btn {
    display: block;
    position: absolute;
    left: 10px;
    top: 17px;
    cursor: pointer;
    z-index: 10;
  }

  .menu-items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    overflow: scroll;
    left: -200%;
    right: 0;
    top: -80px;
    bottom: 0;
    width: 120%;
    height: 120vh;
    background-color: #ffffff;
    z-index: 10;
    transition: left 1s;
    gap: 50px;
    & li {
      text-align: center;
      margin-left: -100px;
    }
  }

  .active {
    left: 0;
  }
  

  .navigationMenu ul li {
    display: none; 
    background-color: #ffffff;
    }

    
    @media screen and (max-width: 767px){
      .navbar {
        display: block;
      }
    }