.viewer {
  z-index: 1;
  width: 100%;
  height: 100%;
  margin-top: 20px;
}

.menuElement {
  color: #222222;
  cursor: pointer;
  // padding: 17px 36px;
  text-align: center;
  background-color: aliceblue;


  & img {
      width: 40px;
  }
}

@media screen and (min-width: 501px) {
  .viewer {
    z-index: 1;
    width: 100%;
    height: 100%;
    margin-top: 20px;
  }
  
  .viewer2{
    display: none;
  }
  .menuElement {
    color: #222222;
    cursor: pointer;
    // padding: 17px 36px;
    text-align: center;
    background-color: aliceblue;
  
  
    & img {
        width: 40px;
    }
  }
}

@media screen and (max-width: 500px) {
  .menuElement {
    & img {
        width: 30px;
    }
}
.viewer2 {
  z-index: 1;
  width: 100%;
  height: 100%;
  margin-top: 20px;
  display: block;
}
.viewer{
  display: none;
}
}
