
  textarea {
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    // box-shadow: -2px 5px 10px -5px #000000bf;
    outline: none;
    border: none;
    resize: none;
  }
  
  textarea::placeholder,
  .output-placeholder {
    color: dimgray;
    user-select: none;
    cursor: text;
    
  }
  
  .input-form {
    position: relative;
  }
  
  .icon-btn {
    position: absolute;
    font-size: 28px;
    cursor: pointer;
    margin: 24.5px 10px;
    transition: 0.2s ease-in-out;
    right: 0;
    top: 20px;
  } 
  
  .translation-error {
    color: #ff1234;
  }
  
  
  @media (max-width: 1279px) {
    .translator {
      width: 100%;
      margin-left: 0%;
    }
  }
  
  @media (max-width: 768px) {
    .translator-container {
      width: 100%;
    }
  
    .row-wrapper {
      flex-direction: column;
    }
  }